<template>
	<view class="content">
		<u-row>
			<u-col span="12" class="tabsList">
				<u-tabs active-color="#fff" inactive-color="#B1D1FF" :list="list" :current="current"
					@change="change"></u-tabs>
			</u-col>
		</u-row>
		<view style="margin: -120rpx 30rpx 0 30rpx;">
			<scroll-view scroll-y style="height: 1100rpx;" v-show="current==0">
				<view class="list01" v-for="(item,index) in list01" :key="index" @click="details(item)">
					<u-row>
						<u-col span="8">
							<view class="blueline"></view>
							<span>{{item.procDefName}}</span>
							<p>申请人：{{item.startUserName}}</p>
							<p>申请时间：{{item.createTime}}</p>
						</u-col>
						<u-col span="4" style="text-align: right;">
							<view style="color:#296DCB">{{item.taskName}}</view>
							<u-button size="mini" class="btn" shape="circle" @click="tocomplete(item)">通过</u-button>
							<u-button size="mini" class="btn" shape="circle" @click="toreject(item)">拒绝</u-button>
						</u-col>
					</u-row>
				</view>
			</scroll-view>
			<scroll-view scroll-y style="height: 1100rpx;" v-show="current==1">
				<view class="list01" v-for="(item,index) in list01" :key="index" @click="details(item)">
					<u-row>
						<u-col span="8">
							<view class="blueline"></view>
							<span>{{item.procDefName}}</span>
							<p>审批时间：{{item.finishTime}}</p>
						</u-col>
						<u-col span="4" style="text-align: right;">
							<view style="color:#296DCB">{{item.taskName}}</view>
							<u-button size="mini" class="btn" shape="circle" @click="toactive(item)"
								v-show="item.objectId=='298'">活动安排</u-button>
						</u-col>
					</u-row>
				</view>
			</scroll-view>
		</view>
		<u-popup v-model="showBox" mode="center" width="85%" height="450upx" border-radius="6" :mask-close-able='false'>
			<view class="popupCon0">
				<view class="popupBtn0">
					<u-icon class="close-img" name="close" @click="showBox=false;spBox={}"></u-icon>
					<view @click="approval">
						确定
					</view>
				</view>
				<u-input v-model="spBox.comment" type='textarea' height='240' border placeholder='请输入审批意见'
					maxlength="200" />
			</view>
		</u-popup>
		<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		todoList,
		finishedList,
		myProcess,
		complete,
		reject,
		zyjcomplete
	} from '../../api/index.js'
	export default {
		data() {
			return {
				spBox: {},
				showBox: false,
				params: {
					pageNum: 1,
					pageSize: 10
				},
				search: 1,
				keyword: '',
				show: 0,
				form: {},
				current: 0,
				swiperCurrent: 0,
				list: [{
					name: '我的待办'
				}, {
					name: '我的已办'
				}],
				list01: [],
			}
		},
		onLoad() {
			this.todo();
		},
		methods: {
			details(item) {
				if (item.objectId == '298') { //活动
					if (uni.getStorageSync('isLeader')) {
						uni.navigateTo({
							url: '../activity/detailstabs?procInstId=' + item.procInsId + '&deployId=' + item
								.deployId
						})
					} else {
						uni.navigateTo({
							url: '../activity/details?procInstId=' + item.procInsId + '&deployId=' + item
								.deployId
						})
					}
				} else if (item.objectId == '291') { //志愿者
					uni.navigateTo({
						url: 'details?procInstId=' + item.procInsId + '&deployId=' + item.deployId
					})
				}
			},
			todo() {
				todoList(this.params).then(res => {
					if (res.code == 200) {
						this.list01 = res.data.data.records;
					}
				})
			},
			finished() {
				finishedList(this.params).then(res => {
					if (res.code == 200) {
						this.list01 = res.data.data.records;
					}
				})
			},
			toactive(item) {
				uni.navigateTo({
					url: 'arrange?id=' + item.procInsId
				})
			},
			approval() {
				if (this.spBox.comment) {
					if (this.spBox.spindex == 1) {
						if (this.spBox.objectId == '291') {
							this.spBox.instanceId = this.spBox.procInsId;
							zyjcomplete(this.spBox).then(res => {
								if (res.code == 200) {
									this.$refs.uToast.show({
										title: '审批成功'
									});
									this.todo();
									this.showBox=false;
									this.spBox={};
								}
							})
						} else {
							complete(this.spBox).then(res => {
								if (res.code == 200) {
									this.$refs.uToast.show({
										title: '审批成功'
									});
									this.todo();
									this.showBox=false;
									this.spBox={};
								}
							})
						}
					} else {
						reject(this.spBox).then(res => {
							if (res.code == 200) {
								this.$refs.uToast.show({
									title: '操作成功'
								});
								this.todo();
								this.showBox=false;
								this.spBox={};
							}
						})
					}
				} else {
					this.$refs.uToast.show({
						title: '请输入审批意见'
					});
				}

			},
			tocomplete(item) {
				this.spBox = item;
				this.spBox.spindex = 1;
				this.showBox=true;
			},
			toreject(item) {
				this.spBox = item;
				this.spBox.spindex = 2;
				this.showBox=true;
			},
			change(index) {
				this.current = index;
				if (index == 1) this.finished();
				else this.todo();
			},
			transition(e) {
				let dx = e.detail.dx;
				this.$refs.uTabs.setDx(dx);
			},
			animationfinish(e) {
				let current = e.detail.current;
				this.$refs.uTabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
		}
	}
</script>

<style scoped>
	.content {
		width: 100vw;
		height: 100vh;
		background-color: #F5F6FA;
	}

	.btn {
		background-color: #296DCB;
		color: #fff;
		font-size: 24rpx;
		margin-top: 40rpx;
	}

	.tabsList {
		background-color: #1F61BE;
		min-height: 203rpx;
		border-radius: 0 0 10rpx 10rpx;
	}

	.tabsList /deep/ .u-tabs {
		background: transparent !important;
	}

	.popupCon0 {
		padding: 33upx 30upx 40upx;
	}

	.popupBtn0 {
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: 34upx;
		font-family: Source Han Sans CN;
		font-weight: 400;
		color: #028CFE;
		line-height: 34upx;
		margin-bottom: 34upx;
	}

	.close-img {
		width: 38rpx;
		height: 38rpx;
		color: #999;
	}

	.topBox {
		position: relative;
		font-size: 32rpx;
		font-weight: bold;
		color: #333333;
	}

	.topBox .ss {
		position: absolute;
		right: -10rpx;
		top: 8rpx;
		width: 30rpx;
		height: 30rpx;
	}

	.grid-text {
		font-size: 24rpx;
		font-weight: bold;
		color: #333333;
		margin-top: 16rpx;
	}

	.list01 {
		width: 100%;
		height: 172rpx;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 20rpx 20rpx 20rpx 30rpx;
		position: relative;
	}

	.blueline {
		position: absolute;
		left: 0;
		top: 30rpx;
		width: 6rpx;
		height: 28rpx;
		background: #0062E9;
	}

	.list01 span,
	.list02 span {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
	}

	.list01 p,
	.list02 p {
		margin-top: 10rpx;
		font-size: 24rpx;
		color: #999999;
		width: 90%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
		/*文本超出省略*/
	}

	.list02 b {
		font-size: 24rpx;
		color: #296DCB;
		font-weight: normal;
	}

	.tiptxt {
		position: absolute;
		right: 32rpx;
		top: 20rpx;
	}

	.listimg {
		width: 208rpx;
		height: 126rpx;
	}

	.list02 {
		width: 100%;
		height: 231rpx;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 17rpx 30rpx;
		position: relative;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
	}

	.bz01 {
		width: 52rpx;
		vertical-align: middle;
		display: inline-block;
		margin-left: -10rpx;
	}

	.bz02 {
		width: 28rpx;
		margin-right: 6rpx;
		vertical-align: middle;
		display: inline-block;
	}
</style>